.webmain .top h3 {
  font-size: 26px;
  font-weight: bold; }
.webmain .top p {
  font-size: 16px;
  line-height: 55px; }

.webmain .chart {
  height: 600px;
  margin-top: 50px; }
  .webmain .chart .submit {
    position: absolute;
    margin-left: 600px; }
    .webmain .chart .submit .confirm {
      float: left;
      width: 75px;
      height: 35px;
      text-align: center;
      line-height: 35px;
      color: #fff;
      font-size: 16px;
      background-color: #000; }
      .webmain .chart .submit .confirm:active {
        background-color: #ccc; }
    .webmain .chart .submit .cancel {
      float: left;
      width: 75px;
      height: 35px;
      margin-left: 15px;
      text-align: center;
      line-height: 35px;
      font-family: Arial;
      color: #fff;
      font-size: 16px;
      background-color: #000; }
      .webmain .chart .submit .cancel:active {
        background-color: #ccc; }
  .webmain .chart dt {
    float: left;
    width: 315px;
    line-height: 35px;
    text-align: right; }
  .webmain .chart dd {
    height: 60px;
    margin-left: 360px; }
    .webmain .chart dd input[type='text'] {
      width: 280px;
      height: 35px;
      line-height: 35px;
      text-indent: 15px;
      border: 1px solid #000;
      background-color: #fff; }
      .webmain .chart dd input[type='text'].w440 {
        width: 440px; }
    .webmain .chart dd textarea {
      width: 440px;
      height: 120px;
      border: 1px solid #000;
      background-color: #fff;
      overflow: visible;
      padding: 15px; }
    .webmain .chart dd input[type='file'] {
      width: 200px; }
    .webmain .chart dd .filewrapper {
      margin-top: 5px; }
